import { useStaticQuery, graphql } from 'gatsby'

// utils
import { groupByDate } from './utils/groupByDate'

/**
 * Get all articles with published date by month
 */
export default function useArticlesByMonth() {
  const {
    allMarkdownRemark: { edges }
  } = useStaticQuery(graphql`
    query allArticlesByMonth {
      allMarkdownRemark(
        filter: {
          fileAbsolutePath: { regex: "/(articles)/.*\\\\.md$/" }
          frontmatter: { published: { ne: "No" } }
        }
        sort: { fields: [frontmatter___date], order: DESC }
      ) {
        edges {
          node {
            frontmatter {
              title
              date(formatString: "MMMM, YYYY")
            }
            fields {
              slug
            }
          }
        }
      }
    }
  `)

  return groupByDate(edges, 'date')
}
