import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Toolbar from '@material-ui/core/Toolbar'
import Fab from '@material-ui/core/Fab'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import LandingPage from '../components/LandingPage/LandingPage'
import ScrollTop from '../components/ScrollTop/ScrollTop'
import Footer from '../components/Footer/Footer'
import useSiteMetaData from '../data/useSiteMetaData'

/**
 * Index page
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function Index({ location, navigate }) {
  const siteMeta = useSiteMetaData()

  return (
    <React.Fragment>
      <SiteHeader title="Home" siteMeta={siteMeta} location={location} navigate={navigate} />
      {/* target for scrollTop component  */}
      <Toolbar id="back-to-top-anchor" />
      <Box pt={8} pb={2}>
        <LandingPage navigate={navigate} />
      </Box>
      <Footer siteMeta={siteMeta} />
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment>
  )
}

export default Index
