import React from 'react'

// material-ui
import CardActionArea from '@material-ui/core/CardActionArea'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import Hidden from '@material-ui/core/Hidden'
import CardMedia from '@material-ui/core/CardMedia'
import { withStyles } from '@material-ui/core/styles'

// custom components
import Link from '../Links/Link'

// Data query function
import useLatestProject from '../../data/useLatestProject'

const styles = {
  card: {
    display: 'flex'
  },
  cardDetails: {
    flex: 1
  },
  cardMedia: {
    width: 160
  }
}

/**
 * Render latest project.
 * @param {Object} props - component properties
 * @param {Object} props.classes - override the injected class names
 * @returns {*}
 * @constructor
 */
function LatestProject({ classes }) {
  const {
    node: {
      excerpt,
      timeToRead,
      fields: { slug },
      frontmatter: { title, date }
    }
  } = useLatestProject()

  return (
    <CardActionArea component={Link} to={slug}>
      <Card className={classes.card}>
        <div className={classes.cardDetails}>
          <CardContent>
            <Typography variant="caption">Latest project</Typography>
            <Typography component="h2" variant="h5" color="inherit">
              {title}
            </Typography>
            <Typography component="div" variant="caption" color="textSecondary">
              {date}
            </Typography>
            <Typography component="div" variant="caption" color="textSecondary" gutterBottom>
              {timeToRead + ' minutes'}
            </Typography>
            <Typography variant="subtitle2" paragraph>
              {excerpt}
            </Typography>
            <Typography variant="subtitle2" color="secondary">
              Continue reading...
            </Typography>
          </CardContent>
        </div>
        <Hidden xsDown>
          <CardMedia
            className={classes.cardMedia}
            image="https://source.unsplash.com/weekly?spring"
            title="Image title"
          />
        </Hidden>
      </Card>
    </CardActionArea>
  )
}

export default withStyles(styles)(LatestProject)
